























import { Action } from 'vuex-class';
import { Component, Ref, Vue } from 'vue-property-decorator';
import { ITestTemplate } from '@/types/interface';
import banner from '../../components/Banner.vue';
import * as commonTypes from '../../types/common';
import inputFormPanel from '../../components/InputFormPanel.vue';

@Component({
  components: {
    banner,
    inputFormPanel,
  },
})

export default class HistoryTest extends Vue {
  @Ref('inputForm') readonly inputForm!: inputFormPanel;
  @Action('getHistoryExamList', { namespace: 'pastTest' }) getHistoryExamList!: (params: ITestTemplate | undefined) => Promise<void>;

  inputList = [commonTypes.INPUT_CK_YEAR_LIST, commonTypes.INPUT_CK_TEST_TYPE,
    commonTypes.INPUT_CK_TEST_LEVEL, commonTypes.INPUT_CK_TEST_SUBJECT,
    commonTypes.INPUT_CK_TEST_COURSE];

  link() {
    this.$router.push('/test/set_condition');
  }

  reset() {
    this.inputForm.resetSelection();
  }

  confirm() {
    const info: ITestTemplate | undefined = this.inputForm.getTestCondition();
    if (info) {
      this.getHistoryExamList(info)
        .then(() => {
          this.$router.push('/test/history_list');
        });
    }
  }
}
